.owc-openkaarten-streetmap {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 20px;
}
.owc-openkaarten-streetmap__row {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
}
.owc-openkaarten-streetmap h3 {
  font-size: 1.2em;
  margin: 0;
}
.owc-openkaarten-streetmap .components-base-control {
  margin-bottom: 20px;
}
.owc-openkaarten-streetmap select option {
  padding: 4px 0;
}
.owc-openkaarten-streetmap p {
  margin: 10px 0;
}
.owc-openkaarten-streetmap .components-message-box {
  display: flex;
  flex-direction: column;
}
.owc-openkaarten-streetmap .components-select-control__input {
  height: auto;
}